#review-section {
  .section__content {
    padding-top: 30px;
  }
  .section__column {
    width: 100%;
  }
}
.reviews {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  position: relative;
  .more_link {
    margin: 30px 0;
  }
}
.more_link {
  &.hide {
    opacity: 0;
  }
  a {
    font-size: 17px;
    color: $primaryColor;
    transition: 0.3s all ease-in-out;
    font-weight: 600;
    font-family: "Montserrat";
    &:hover {
      color: $textColor;
    }
  }
}
.img__wrapper {
  width: 100%;
  height: 100%;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
.reviews__list {
  display: flex;
  height: 100%;
  flex-wrap: wrap;
  position: relative;
  transition: 1s all;
  &.hide {
    position: absolute;
    top: 110px;
    opacity: 0;
    z-index: -1;
    transform: translateY(50%);
  }
}
.reviews__item {
  transition: 1s all;
  display: flex;
  align-items: flex-start;
  padding: 0 50px;
  width: 50%;
  height: 150px;
  overflow: hidden;
  margin-bottom: 30px;
  &.active {
    height: 100%;
    opacity: 1;
    display: flex;
  }
  > div + div {
    margin-left: 10px;
  }
}
.reviews__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 700;
  font-family: "Montserrat", sans-serif;
  .date-time {
    font-style: italic;
    color: #808080;
  }
}
.reviews__content {
  > div + div {
    margin-top: 10px;
  }
}
.reviews__desc {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  text-align: justify;
  font-size: 15px;
}
.reviews__mark {
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  span {
    font-weight: 700;
    color: green;
  }
}
.reviews__img {
  img {
    height: 64px;
    width: 64px;
    object-fit: cover;
    border-radius: 50%;
  }
}
.reviews__sumary {
  font-size: 15px;
  font-weight: 700;
  span {
    font-family: "Montserrat", sans-serif;
  }
}
.video-review {
  text-align: center;
  width: 100%;
}
.video-review__button {
  text-align: center;
  width: 300px;
  height: 50px;
  margin: 0 auto;
  margin-bottom: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 18px;
  color: white;
  position: relative;
  background-color: $primaryColor;
  z-index: 3;
  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 0;
    z-index: -1;
    background-color: #ff0000;
    transition: 1s all;
  }
  &.video {
    &::before {
      font-family: FontAwesome;
      content: "\f16a ";
      left: 10%;
      font-size: 25px;
      margin-right: 15px;
    }
    &::after {
      width: 100%;
      z-index: -1;
    }
  }
}
.video-review__wrapper {
  position: absolute;
  transform: translateY(50%);
  opacity: 0;
  width: 100%;
  z-index: -1;
  transition: 1s all;
  display: flex;
  justify-content: center;
  &.active {
    position: relative;
    display: flex;
    opacity: 1;
    z-index: 5;
    transform: translateY(0);
  }
}
.youtube-video {
  width: 600px;
  height: 315px;
}
@media all and(max-width:660px) {
  .youtube-video {
    width: 99%;
    height: 200px;
  }
} /*
@media all and(max-width:550px) {
  .youtube-video {
    width: 400px;
  } 
  .video-review__wrapper {
    margin-left: -200px;
  }
} */
@media all and(max-width:768px) {
  .reviews__item {
    width: 100%;
  }
}
@media all and (max-width: 425px) {
  .reviews__item {
    padding: 0 15px;
  }
}
