.prices {
  width: 100%;
  padding: 0 50px;
}
.prices__title {
  text-align: center;
  font-size: 18px;
  font-family: "Montserrat", sans-serif;
  font-weight: 700;
  padding: 10px 0;
  background: $primaryColor;
  color: $textColor;
}
/*
.prices__subTitle{
    font-size: 14px;
    margin:10px 0;
    font-weight: 700;
    text-align: center;
    color:#808080;
} */
.prices__table {
  width: 100%;
}
.prices__row {
  display: flex;
  width: 100%;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  & + .prices__row {
    margin-top: 5px;
  }
}
.prices__column {
  flex: 1 1 70%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  background: white;
  p {
    text-align: center;
  }
  .price {
    font-weight: 600;
    font-size: 16px;
  }
  &:last-child {
    flex: 1 1 30%;
  }
}
.price-column {
  border-left: 3px solid $primaryColor;
}
@media all and (max-width: 425px) {
  .prices {
    padding: 0 15px;
  }
}
