.footer {
  position: fixed;
  bottom: 0;
  height: 30px;
  background-color: rgba(0, 0, 0, 0.9);
  width: 100%;
  z-index: 999;
  display: flex;
  font-size: 16px;
  align-items: center;
  .section__column {
    height: 30px;
    align-items: center;
    display: flex;
    padding: 0 30px;
    color: white;
    &:first-child {
      justify-content: flex-end;
      border-right: 2px solid $primaryColor;
    }
  }
  .section__column + .section__column {
    margin-left: 0;
  }
  &__address {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: white;
    .fa {
      color: $primaryColor;
    }
    address {
      font-style: normal;
      margin-left: 10px;
    }
  }
}
.contact-form {
  display: flex;
  opacity: 0;
  width: 250px;
  justify-content: center;
  align-items: center;
  height: 40px;
  z-index: -5;
  background-color: $primaryColor;
  color: white;
  font-weight: 700;
  font-size: 18px;
  position: fixed;
  bottom: 60px;
  right: 60px;
  border-radius: 5px;
  transition: 0.5s all ease;
  &.active {
    z-index: 99;
    opacity: 1;
  }
}
.modal-block {
  background: rgba(0, 0, 0, 0.8);
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -5;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  opacity: 0;
  transition: 0.3s all linear;
  .modal-block__content {
    opacity: 0;
  }
  &.open {
    opacity: 1;
    z-index: 999;
    .modal-block__content {
      opacity: 1;
    }
  }
}
.modal-block__open {
  cursor: pointer;
}
.modal-block__end {
  position: absolute;
  width: 100%;
  cursor: pointer;
  height: 250px;
  background: white;
  top: 50%;
  margin-top: -125px;
  left: 0;
  border: 1px solid black;
  z-index: -5;
  font-size: 18px;
  font-weight: 500;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  opacity: 0;
  &.show {
    z-index: 1000;
    opacity: 1;
    & + .modal-block__content {
      opacity: 0;
    }
  }
  span {
    color: #888;
    font-weight: 600;
  }
}
.modal-block__titleBlock {
  flex-basis: 100%;
}
.modal-block__title {
  font-size: 25px;
  color: black;
  font-weight: 600;
  text-align: center;
  position: relative;
  &::after {
    position: absolute;
    content: "";
    height: 2px;
    width: 200px;
    background-color: $primaryColor;
    bottom: -5px;
    left: 50%;
    margin-left: -100px;
  }
}
.modal-block__subTitle {
  font-size: 18px;
  text-align: center;
  margin-top: 15px;
  color: black;
}
.modal-block__content {
  width: 600px;
  height: 500px;
  background: rgba(white, 1);
  padding: 30px 30px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  border-radius: 5px;
}
.modal-block__close {
  position: absolute;
  right: 15px;
  top: 15px;
  cursor: pointer;
  &:hover {
    .fa {
      color: rgba(black, 0.6);
    }
  }
  .fa {
    font-size: 20px;
    color: black;
  }
}
#contact-form {
  justify-self: center;
  flex-basis: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.submit {
  flex-basis: 100%;
  align-items: flex-end;
  display: flex;
  justify-content: center;
}
.submit__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 40px;
  border: 2px solid $primaryColor;
  color: white;
  font-size: 18px;
  background-color: $primaryColor;
  font-weight: 600;
}
.input__wrapper {
  display: flex;
  margin-bottom: 15px;
  align-items: center;
  // border-bottom: 1px solid rgba(0, 0, 0, 0.8);
  // border-top: 2px solid rgba(0, 0, 0, 0.8);
  position: relative;
  &::after {
    content: "";
    width: 0;
    display: flex;
    position: absolute;
    height: 2px;
    transition: 0.5s all linear;
    bottom: 0px;
    right: 0;
  }
  &.error {
    &::after {
      position: absolute;
      width: calc(50% + 2px);
      height: 2px;
      background-color: red;
    }
  }
  &.validate {
    &::after {
      position: absolute;
      width: calc(50% + 2px);
      height: 2px;
      background-color: green;
    }
  }
  &.focused {
    &::after {
      position: absolute;
      width: calc(50% + 2px);
      height: 2px;
      background-color: $primaryColor;
    }
  }
  label {
    flex-basis: 100%;
    font-weight: 500;
    color: black;
    // border-right: 1px solid rgba(0, 0, 0, 0.5);
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 15px;
  }
  textarea {
    resize: none;
  }
  input,
  textarea {
    padding: 10px;
    // border-left: 1px solid rgba(0, 0, 0, 0.5);
    padding-left: 15px;
    flex-basis: 100%;
    background: none;
    transition: 0.5s all linear;
    font-weight: 600;
    &:focus {
      color: black;
    }
    &::placeholder {
      color: rgba(black, 0.6);
    }
  }
}
@media all and(max-width:768px) {
  .footer {
    .section__column:last-child {
      display: none;
    }
    .section__column:first-child {
      border-right: none;
    }
  }
  .modal-block__content {
    width: 100%;
    margin: 0 30px;
  }
}
@media all and(max-width:505px) {
  .input__wrapper {
    flex-direction: column;
    align-items: flex-start;
    &.error::after,
    &.validate::after,
    &.focused::after {
      width: 100%;
    }
    label {
      margin-bottom: 10px;
      width: 100%;
      align-items: flex-start;
      justify-content: flex-start;
    }
  }
  .modal-block__titleBlock,
  .submit {
    flex-basis: unset;
  }
}
