.welcome {
  position: relative;
  height: 100vh;
  width: 100%;
  background: url(../img/crissXcross.png);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3);
    z-index: 0;
  }
  &__text {
    margin-top: 60px;
    position: relative;
    z-index: 3;
    font-size: 25px;
    width: 50%;
    .bold{
      color:$primaryColor;
    }
    .text {
      text-align: center;
      color: white;
      line-height: 1.3;
    }
    .text + .text {
      margin-top: 15px;
    }
  }
}
.logo {
  position: relative;
  z-index: 3;
  text-align: center;
}
@media all and(max-width:768px) {
  .welcome__text {
    font-size: 25px;
    width: 80%;
  }
}
@media all and(max-width:550px) {
  .logo {
    img {
      max-width: 100%;
    }
  }
}
@media all and(max-width:375px) {
  .welcome__text {
    font-size: 20px;
  }
}
