.section {
  padding: 60px 0;
  animation-name: show;
  animation-duration: 1s;
  &:nth-child(2n) {
    background: #eeeeee;
  }
  &__title {
    font-size: 2.2rem;
    text-transform: uppercase;
    font-weight: 900;
    text-align: center;
    position: relative;
    display: flex;
    justify-content: center;
    padding-bottom: 20px;
    &:after {
      content: "";
      position: absolute;
      width: 100px;
      height: 5px;
      border-radius: 25px;
      bottom: 0px;
      background: $primaryColor;
    }
  }
  &__sub-title {
    padding: 15px 0;
    font-size: 18px;
    text-align: center;
    width: 80%;
    margin: 0 auto;
    font-weight: 600;
    .underline-text {
      color: #888;
      text-decoration: underline;
      transition: 0.5s ease;
      &:hover {
        color: $primaryColor;
      }
    }
  }
  &__content {
    padding-top: 60px;
  }
  &__row {
    display: flex;
    align-items: center;
    &.btn-row {
      padding-top: 60px;
    }
  }
  &__column {
    flex-basis: 100%;
    .btn {
      margin-top: 60px;
    }
  }
  &__column + &__column {
    margin-left: 60px;
  }
  &__img {
    max-width: 100%;
  }
}
@media all and(max-width:768px) {
  .section__row {
    align-items: center;
    flex-direction: column;
  }
  .section__column {
    & + & {
      margin-left: 0;
      margin-top: 30px;
    }
  }
}
@keyframes show {
  from {
    opacity: 0;
    transform: translateY(50%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
