.main-nav {
  border-bottom: 2px solid #888;
  position: fixed;
  width: 100%;
  top: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 999;
  height: 50px;
  &__logo {
    img {
      height: 40px;
    }
  }
  &__list {
    height: 50px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  &__link {
    color: white;
    font-weight: 700;
    text-transform: uppercase;
    &:hover {
      color: $primaryColor;
    }
  }
}
.mobile-menu.active + .main-nav__list {
  opacity: 1;
  transform: translate(0, 0);
}
.mobile-menu {
  height: 50px;
  width: 33px;
  display: none;
  align-items: center;
  justify-content: center;
  position: relative;
  flex-direction: column;
  z-index: 5;
  &::before,
  &::after {
    position: absolute;
    content: "";
    display: block;
    width: 33px;
    height: 5px;
    background-color: white;
    border-radius: 5px;
  }
  &::before {
    transform: translateY(-10px);
  }
  &::after {
    transform: translateY(10px);
  }
  &.active {
    transform: translate(-15px, -10px);
    &::after,
    &::before {
      background-color: white;
      width: 20px;
    }
    &::after {
      transform: rotate(45deg) translate(0, 0);
    }
    &::before {
      transform: rotate(-45deg) translate(0, 0);
    }
    .mobile-menu__burger {
      background-color: white;
      transform: translate(-10px, 0);
      opacity: 0;
    }
  }
  &__burger {
    width: 33px;
    height: 5px;
    background-color: white;
    border-radius: 5px;
    z-index: 5;
    position: absolute;
    cursor: pointer;
  }
}
@media all and(max-width:850px) {
  .main-nav {
    &__link {
      font-size: 12px;
    }
  }
}
@media all and(max-width:768px) {
  .logo-mobile {
    display: flex;
  }
  .container {
    padding: 0 30px;
  }
  .mobile-menu,
  .logo-mobile,
  .social-list-mobile {
    flex-basis: 100%;
  }
  .mobile-menu {
    display: flex;
    align-items: flex-start;
  }
  .social-list-mobile {
    justify-content: flex-end;
  }
  .main-nav {
    .container {
      display: flex;
      justify-content: space-between;
      padding: 0 15px;
    }
    &__logo {
      display: none;
    }
    &__list {
      opacity: 0;
      position: fixed;
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      width: 400px;
      border-radius: 0;
      height: 400px;
      transition: 0.5s all ease;
      transform: translate(-100%, -100%);
      padding-left: 0px;
      padding-top: 50px;
      &::before {
        content: "";
        position: absolute;
        height: 100%;
        width: 100%;
        z-index: -1;
        background-color: rgba($darkPrimaryColor, 1);
        border: 3px solid white;
        border-radius: 50%;
        transform: translate(-200px, -100px);
      }
    }
    &__item + &__item {
      margin-top: 15px;
    }
    &__link {
      color: white;
    }
  }
  .social-list {
    &-mobile {
      display: flex;
    }
  }
}
